import { Component, Inject, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Family } from './family.model';
import { FamilyService } from '../family.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'fury-family-create-update',
  templateUrl: './family-create-update.component.html',
  styleUrls: ['./family-create-update.component.scss']
})
export class FamilyCreateUpdateComponent implements OnInit {
  @Input()
  family: Family;
  static id = 100;

  form: FormGroup;
  mode: 'create' | 'update' = 'create';

  constructor(@Inject(MAT_DIALOG_DATA) public defaults: any,
    private dialogRef: MatDialogRef<FamilyCreateUpdateComponent>,
    private fb: FormBuilder, private familyService: FamilyService) {
  }

  ngOnInit() {
    this.mode = this.defaults ? 'update' : 'create';

    this.form = this.fb.group({
      id: [this.defaults?.id],
      name: [this.defaults?.name || '', Validators.required],
    });
  }


  save() {
    if (this.mode === 'create') {
      this.createFamily();
    } else if (this.mode === 'update') {
      this.updateFamily();
    }
  }
  
  // private handleValidationErrors(errors: { [key: string]: string }): void {
  //   Object.keys(errors).forEach((field) => {
  //     const control = this.form.get(field);
  //     if (control) {
  //       control.setErrors({ serverError: errors[field] });
  //     }
  //   });
  // }

  private handleValidationErrors(errors: { [key: string]: string }): void {
    console.log('Validation errors:', errors);
    Object.keys(errors).forEach((field) => {
      const control = this.form.get(field);
      if (control) {
        control.setErrors({ serverError: errors[field] });
        console.log(`Updated form control '${field}' with error:`, control);
      }
    });
  }

  createFamily() {
    const family = this.form.value;
    this.familyService.create(family).subscribe((createdFamily) => {
      this.dialogRef.close(createdFamily);
    },
    (error) => {
      if (error instanceof HttpErrorResponse && error.status === 400) {
        this.handleValidationErrors(error.error);
      }
    }
  );
  }

  updateFamily() {
    const family = this.form.value;
    this.familyService.upsert(family.id, family).subscribe(() => {
      this.dialogRef.close(family);
    },
    (error) => {
      // Handle validation errors and update form controls
      if (error instanceof HttpErrorResponse && error.status === 400) {
        this.handleValidationErrors(error.error);
      }
    }
  );
  }

  isCreateMode() {
    return this.mode === 'create';
  }

  isUpdateMode() {
    return this.mode === 'update';
  }
}
