import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Observable, of, ReplaySubject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ListColumn } from 'src/@fury/shared/list/list-column.model';
import { ALL_IN_ONE_TABLE_DEMO_DATA } from '../pages/tables/all-in-one-table/all-in-one-table.demo';
import { FamilyCreateUpdateComponent } from './family-create-update/family-create-update.component';
import { Family } from './family-create-update/family.model';
import { fadeInRightAnimation } from 'src/@fury/animations/fade-in-right.animation';
import { fadeInUpAnimation } from 'src/@fury/animations/fade-in-up.animation';
import { FamilyService } from './family.service';
import { NavigationEnd, Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'fury-family',
  templateUrl: './family.component.html',
  styleUrls: ['./family.component.scss'],
  animations: [fadeInRightAnimation, fadeInUpAnimation]
})
export class FamilyComponent implements OnInit, AfterViewInit, OnDestroy {
  
  @Input() modelName: string;

  subject$: ReplaySubject<Family[]> = new ReplaySubject<Family[]>(1);
  data$: Observable<Family[]> = this.subject$.asObservable();
  families: Family[];
  config = 'http://localhost:2203/api';
  @Input()
  columns: ListColumn[] = [
    { name: 'Checkbox', property: 'checkbox', visible: false },
    { name: 'Id', property: 'id', visible: true, isModelProperty: true, sortable: true },
    { name: 'Name', property: 'name', visible: true, isModelProperty: true },
    { name: 'Actions', property: 'actions', visible: true },
  ] as ListColumn[];
  pageSize = 10;
  dataSource: MatTableDataSource<Family> | null;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  

  constructor(private dialog: MatDialog, private familyService: FamilyService,private router: Router, private httpClient: HttpClient) {
    console.log('modelName:', this.modelName);
     // override the route reuse strategy
     this.router.routeReuseStrategy.shouldReuseRoute = function(){
      return false;
   }

   this.router.events.subscribe((evt) => {
      if (evt instanceof NavigationEnd) {
         // trick the Router into believing it's last link wasn't previously loaded
         this.router.navigated = false;
         // if you need to scroll back to top, here is the right place
         window.scrollTo(0, 0);
      }
  });
  }

  get visibleColumns() {
    return this.columns.filter(column => column.visible).map(column => column.property);
  }



  ngOnInit() {
    this.familyService.modelName = this.modelName;
    console.log('userId is:',this.modelName);
    this.loadFamilies();
  }

  private loadFamilies() {
    this.dataSource = new MatTableDataSource<Family>();
    this.familyService.findAll().subscribe((families) => {
      this.families = families;
      this.dataSource.data = families;
    });
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    setTimeout(() => {
      this.sort.active = 'id';
      this.sort.direction = 'asc';
    });
  }

  reloadComponent() {
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
    this.router.navigate(['/same-route']);
  }
  

  createFamily() {
    this.dialog.open(FamilyCreateUpdateComponent).afterClosed().subscribe((family: Family) => {
      if (family) {
        this.familyService.create(family).subscribe((createdFamily) => {
          this.families.unshift(createdFamily);
          this.dataSource.data = this.families;
        });
      }
    });
  }

  updateFamily(family) {
    this.familyService.findById(family.id).subscribe((currentFamily: Family) => {
      const dialogRef = this.dialog.open(FamilyCreateUpdateComponent, { data: currentFamily });

      dialogRef.afterClosed().subscribe((updatedFamily: Family) => {
        if (updatedFamily) {
          this.familyService.upsert(family.id, updatedFamily).subscribe(() => {
            const index = this.families.findIndex((existingFamily) => existingFamily.id === updatedFamily.id);
            if (index !== -1) {
              this.families[index] = updatedFamily;
              this.dataSource.data = this.families;
            }
          });
        }
      });
    });
  }

   deleteFamily(family) {
    this.familyService.deleteById(family.id).subscribe(() => {
      this.families = this.families.filter((existingFamily) => existingFamily.id !== family.id);
      this.dataSource.data = this.families;
    });
  }
  
  onFilterChange(value) {
      if(!this.dataSource) {
      return;
    }
    value = value.trim();
    value = value.toLowerCase();
    this.dataSource.filter = value;
  }

  ngOnDestroy() {
  }
}
